import {common} from "../../utils/api/url";
import { Dialog } from 'vant';
import { Toast } from 'vant';

import mixins from "~/utils/mixins";
import mixinsBridge from "~/utils/mixinsBridge";
export default {
  name: 'detailIndex',
  layout: 'defaultAppScreen',
  mixins: [mixins,mixinsBridge],
  data(){
    return {
      active: 6,
      topHeight: 0,
      serverAppList: [],
      noticeAppList: [],
      pageType: '',
      userId: '',
      detailData: '',
      signStatus: '',
      tablePayData: [],
      tableSignData: [],
      container: null,
      signLoading: false,
      payLoading: false,
      checkinId: ''
    }
  },
  mounted() {
    this.container = this.$refs.container;
  },
  created() {
    this.userId = this.$route.query.userId;
    this.topHeight = this.navHeight > 0 ? (parseInt(0) + parseInt(this.navHeight)) : 10;
    console.log(this.userId);
    if (this.$route.query.sessionId){
      this.initAppConfig();
    }else{
      this.pageType = this.$route.query.type ? this.$route.query.type : 'server';
      this.initAppServer();
    }
  },
  methods: {
    layoutInit(){

    },
    async initAppConfig(){
      await this.autoLoginApp();
      //await this.getSessionInfo();
      setTimeout(()=>{
        this.initAppServer();
      },1000);
    },
    initAppServer(){
      this.initInfo();
      this.initPay();
      this.initSignInfo();
    },
    initInfo(){
      let params = {
        userId: this.userId
      };
      this.$axios.get(common.enroll_checkin_student_detail2, {params: params}).then(res => {
        if (res.data.data){
          this.detailData = res.data.data;
        }
      });
    },
    initPay(item){
      let params = {
        userId: this.userId
      };
      this.$axios.get(common.enroll_checkin_pay_list_by_user, {params: params}).then(res => {
        if (res.data.data){
          for (let i = 0; i < res.data.data.length; i++){
            res.data.data[i]['wait_amount'] = res.data.data[i].should_amount - res.data.data[i].paid_amount;
          }
          this.tablePayData = res.data.data;
        }
      });
    },
    initSignInfo(){
      let params = {
        userId: this.userId
      };
      this.signStatus = false;
      this.$axios.get(common.server_enroll_app_student_checkin_get, {params: params}).then(res => {
        if (res.data.data){
          let data = JSON.stringify(res.data.data);
          if (data != "{}"){
            this.signStatus = res.data.data.check_status;
            this.checkinId = res.data.data.id;
            this.tableSignData = [res.data.data];
          }
        }
      });
    },
    payConfirm(event, type){
      Dialog.confirm({
        title: '提示',
        message: '确认执行该操作？',
      }).then(() => {
          // on confirm
        let url = '';
        let valObj = {};
        let params = '';

        if (type == 3){
          url = common.enroll_payment_set_student_payment;
          valObj = {
            paidAmountValList: [],
            paymentStatus: type,
            paymentType: 1,
            userId: this.userId
          };
          this.$axios.post(url, JSON.stringify(valObj), {dataType: 'stringfy', loading: false}).then(res => {
            if (res.data.code == 200){
              this.initAppServer();
              Toast(res.data.desc);
            }else {
              Toast(res.data.desc);
            }
            this.payLoading = false;
          });
        }else if (type == 1){
          url = common.enroll_payment_set_student_revoke;
          valObj = {
            userId: this.userId
          };
          valObj = this.$qs.stringify(valObj);
          this.$axios.post(url, valObj, {loading: false}).then(res => {
            if (res.data.code == 200){
              this.initAppServer();
              Toast(res.data.desc);
            }else {
              Toast(res.data.desc);
            }
            this.payLoading = false;
          });
        }
        this.payLoading = true;

      }).catch(() => {
          // on cancel
        this.payLoading = false;
      });
    },
    signConfirm(){
      Dialog.confirm({
        title: '提示',
        message: '确认执行该操作？',
      }).then(() => {
        // on confirm
        let params = {
          userId: this.userId
        };

        let url = common.enroll_checkin_handle;
        params = this.$qs.stringify(params);
        this.signLoading = true;
        this.$axios.post(url, params).then(res => {
          if (res.data.code == 200){
            this.initAppServer();
            Toast(res.data.desc);
          }else {
            Toast(res.data.desc);
          }
          this.signLoading = false;
        });
      }).catch(() => {
        // on cancel
        this.signLoading = false;
      });
    },
    unSignConfirm(){
      Dialog.confirm({
        title: '提示',
        message: '确认执行该操作？',
      }).then(() => {
        // on confirm
        let params = {
          checkinId: this.checkinId
        };

        let url = common.enroll_checkin_revoke;
        params = this.$qs.stringify(params);
        this.signLoading = true;
        this.$axios.post(url, params).then(res => {
          if (res.data.code == 200){
            this.initAppServer();
            Toast(res.data.desc);
          }else {
            Toast(res.data.desc);
          }
          this.signLoading = false;
        });
      }).catch(() => {
        // on cancel
        this.signLoading = false;
      });
    },
    scanRecord(){
      this.$router.push({
        path: '/studentDetail/detailRecord',
        query: {
          userType: this.loginUserAppType,
          navH: this.navHeight,
          appType: this.globalAppShow,
          sessionId: this.$route.query.sessionId,
          userId: this.$route.query.sessionId,
        }
      });
    },
    returnIndex(){
      this.returnGlobalMain(1);
    }
  }
}
